.modal-backdrop {
    position: fixed;
    visibility: visible;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $hero-bg;
    z-index: 500;
    overflow: auto;

    &__modal {
        position: absolute;
        top: 10%;
        left: 50%;
        transform: translateX(-50%) scale(1);
        width: calc(100% - 80px);

        transition: all 700ms;
        background-color: $polar;
        box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12),
            0px 1px 1px rgba(0, 0, 0, 0.14),
            0px 2px 1px rgba(0, 0, 0, 0.2);
        border-radius: 4px;

        @include breakpoint(mobile-only) {
            min-width: 300px;
        }

        @include breakpoint(mobile) {
            max-width: 450px;
        }

        @include breakpoint(desktop) {
            max-width: 528px;
        }
    }
}

.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: 300ms linear 700ms;
}

.modal-form {
    margin: 40px;

    &__title {
        font: normal 400 16px/1.25em 'DM Sans',
            sans-serif;
        color: $main;
        margin-bottom: 12px;
        text-align: center;

        li {
            list-style-type: circle;
        }
    }

    &__filling {
        display: flex;
        flex-direction: column;
        margin-bottom: 10px;
    }

    &__label {
        font: normal 400 12px/1.16em 'DM Sans',
            sans-serif;
        color: $primary;
        margin-bottom: 4px;
        text-align: left;
    }

    &__label:first-child {
        margin-top: 8px;
    }

    &__list {
        padding-left: 20px;
        text-align: left;
    }

}

.modal-form__label--color {
    @extend .modal-form__label;
    color: $main;
}

.modal-form-focus {
    transition: outline $tt $tf;
}

.modal-form-focus:focus-within {
    border: $btn1-hover;
    fill: $btn1-hover;
    outline-color: $btn1-hover;
}

.modal-form-field {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    padding-bottom: 10px;

    &__content {
        border: 1px solid $btn1-hover;
        box-sizing: border-box;
        border-radius: 4px;
        height: 40px;
        padding-left: 40px;
        fill: $btn1-hover;
        outline-color: $btn1-hover;
    }

    &__span {
        position: absolute;
        top: auto;
        right: auto;
        bottom: 12px;
        left: auto;
        transform: translateY(-75%);
        display: block;
        width: 18px;
        height: 18px;
        transition: fill $tt $tf;
    }

    &__icon {
        position: absolute;
        top: 50%;
        right: auto;
        bottom: auto;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    &__textarea {
        @extend .modal-form-field__content;
        padding: 12px 16px;
        width: 100%;
        height: 120px;
    }

    &__textarea::placeholder {
        font: normal 400 12px/1.16em 'DM Sans',
            sans-serif;
        color: rgba(117, 117, 117, 0.5);
    }

}

