.modal-button-close {
    position: absolute;
    top: 8px;
    right: 8px;

    &__icon-close {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        background: $main;
        border: 1px solid rgba(0, 0, 0, 0.1);
      //  --color1: $btn1-hover;
    }

    &__icon-close:hover,
    &__icon-close:focus {
        background: $main;
        //--color1: $btn2-hover;
        box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12),
        0px 1px 1px rgba(0, 0, 0, 0.14),
        0px 2px 1px rgba(0, 0, 0, 0.2);
        outline: 1px rgba(0, 0, 0, 0.1);
    }
}
.modal__button {
    font: normal 400 12px/1.25em 'DM Sans', sans-serif;
    min-width: 62.4px;
    height: 24px;
    text-align: center;
    border-radius: 20px;
    border: none;
    background: $btn1-prod-loc;
    color: $btn2-franchise;
    transition: all 150ms $tf;

    // transition: background-color $time $time-function;
    @include breakpoint(desktop) {
        font: normal 400 14px/1.29em 'DM Sans', sans-serif;
        width: 104px;
        height: 40px;
        margin-left: 38px;
    }

    &:hover,
    &:focus {
        transform: scale(1.1);
        background: $btn1-hover;
        color: $btn2-franchise;
    }
}
.modal__button--poz {
    display: flex;
    justify-content: center;
   
}